.footer{
    margin-top: 40px;
    background-color: rgba(247, 247, 247, 1);
    display: flex;
    flex-direction: column;
    gap: 9.9px;
    align-items: center;
    font-size: 16px;
    font-weight: 400;
    line-height: 19px;
    letter-spacing: 0em;
   
@media screen and (min-width: 768px) {
        flex-direction: row;
        justify-content: center;
        gap:4px;
    }

    
}

.footer-container1{
    margin-left: 0;
    margin-right: 0;
    width: 320px;
    height: 100px;
    
    @media screen and (min-width: 768px) {
        width: 768px;
        height: 77px;
    
    }
    
        @media screen and (min-width: 1024px) {
        width: 1024px;
    
        }
        
}

.footer-text{
    padding-top: 20px;
    margin: 0;
        @media screen and (min-width: 768px) {
            padding : 0;
    
        }
    
        
}

.students{
    text-decoration: none;
    text-decoration: underline;
}